import React from 'react'
import ScrollableAnchor from 'react-scrollable-anchor'
import PrimaryButton from '../Buttons/PrimaryButton'
import classnames from 'classnames'



class WorkElement extends React.Component {

  render() { 
      return (
      <ScrollableAnchor id={this.props.id}>
        <div className={this.props.containerClass}>
          
          <div className="page-header">
              <h2>{this.props.pageHeader}</h2>
            </div>
          <div className="work-content">
          <a href={this.props.upAnchor}>
            <i className={classnames('fas fa-chevron-up', this.props.upClass)}></i>
          </a>
          <div>
              <img className="image" src={require(`${this.props.image}`)} />
              <video className="video" controls loop playsInline muted autoPlay data-reactid=".0.1.0.0">
                <source type="video/mp4" data-reactid=".0.1.0.0.0" src={this.props.videoURL}></source>
              </video>
              </div>
              <div className={this.props.descriptionClass}>
                <h3>{this.props.header}</h3>
                <ul className="work-list">
                {this.props.skills.map(skill => <li key={skill}>{skill}</li>)}
                </ul>
                <p>{this.props.description}</p>
                <a target="_blank" href="http://stolberg.com.au/">
                  <PrimaryButton className={this.props.buttonClass} label="Visit Site" />
                </a>
              </div>
              <a href={this.props.downAnchor}>
            <i className={classnames('fas fa-chevron-down', this.props.downClass)}></i>
          </a>
          </div>
          
        </div>
      </ScrollableAnchor>
      )
  }
}

export default WorkElement

