import React from 'react'
import { NavLink } from 'react-router-dom'
import { push as Menu } from 'react-burger-menu'
import Close from './images/close.svg'
import './style.scss'


class BurgerMenu extends React.Component {
  constructor (props) {
    super(props)
    this.state = {
      menuOpen: false
    }
  }

  // This keeps your state in sync with the opening/closing of the menu
  // via the default means, e.g. clicking the X, pressing the ESC key etc.
  handleStateChange (state) {
    this.setState({menuOpen: state.isOpen})  
  }
  
  // This can be used to close the menu, e.g. when a user clicks a menu item
  closeMenu () {
    this.setState({menuOpen: false})
  }

  // This can be used to toggle the menu, e.g. when using a custom icon
  // Tip: You probably want to hide either/both default icons if using a custom icon
  // See https://github.com/negomi/react-burger-menu#custom-icons
  toggleMenu () {
    this.setState({menuOpen: !this.state.menuOpen})
  }
 
  render () {
    return (
      <div>
      <div 
        onClick={() => this.toggleMenu()}
        className="burger">
        <div></div>
        <div></div>
        <div></div>
      </div>
      
      <Menu
      right
      isOpen={this.state.menuOpen}
      onStateChange={(state) => this.handleStateChange(state)}
      pageWrapId={ "page-wrap" } outerContainerId={ "outer-container" }
      customBurgerIcon={ false }
      customCrossIcon={ <Close /> }
      id={ "sidebar" } className={ "my-menu" }
      width={ 250 }
      >
      <NavLink to="/about" activeClassName="is-active" exact={true} onClick={() => this.closeMenu()}>About</NavLink>
      <NavLink to="/services" activeClassName="is-active" exact={true} onClick={() => this.closeMenu()}>Services</NavLink>
      <NavLink to="/work" activeClassName="is-active" exact={true} onClick={() => this.closeMenu()}>Work</NavLink>
      <NavLink to="/contact" activeClassName="is-active" exact={true} onClick={() => this.closeMenu()}>Contact</NavLink>
      </Menu>
      </div>
    );
  }
}

export default BurgerMenu